import * as React from "react";
import { observer } from "mobx-react";
import { serialData } from "./serial-data";
import { SignalReading } from "./gesture-data";
import { observable } from "mobx";



@observer
export class DeviceOrientation extends React.Component<{}, {}> {
    @observable private latestReading: SignalReading;
    private avgRoll = createMovingAverage();
    private avgPitch = createMovingAverage();

    componentDidMount() {
        serialData.register(data => {
            this.latestReading = data.accVec;
            console.log(this.latestReading.X, this.latestReading.Y, this.latestReading.Z);
        });
    }

    public render() {
        let roll = 0, pitch = 0;
        const acc = this.latestReading;
        if (this.latestReading) {
            // https://www.dfrobot.com/wiki/index.php/How_to_Use_a_Three-Axis_Accelerometer_for_Tilt_Sensing
            roll = Math.atan2(acc.Y, acc.Z);
            pitch = Math.atan2((-acc.X), Math.sqrt(acc.Y * acc.Y + acc.Z * acc.Z));
        }
        const imgStyle: React.CSSProperties = {
            transformStyle: "preserve-3d",
            transform: "rotateX(" + (this.avgRoll(roll) - Math.PI) + "rad) rotateY(" + (this.avgPitch(pitch)) + "rad)",
            padding: 0
        };
        return (
            <div style={{ perspective: "500px" }}>
                <img src="/circuitplayground-front.png" width={200} height={200} style={imgStyle} />
            </div>
        );
    }
}


const createMovingAverage = () => {
    // const NUM_SAMPLES = 3;
    // const values: number[] = []
    return (value: number) => {
        return value;
        // values.push(value);
        // if (values.length > NUM_SAMPLES) {
        //     values.shift();
        // }
        // var sum = values.reduce((s, v) => s + v);
        // return sum / values.length;
    }
}



